.main-button{
    border: 1px #fff solid;
    width: fit-content;
    padding: 10px 16px;
    border-radius: 8px;
    color: #fff;
    font-weight: 600;
    font-family: 'Proxima Soft', sans-serif;
    font-size: 12px;
    text-transform: uppercase;
    -webkit-transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
    transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
}

.main-button:hover{
    box-shadow: 0 0 40px 40px #fff inset;
    color: #000;
}

.button-blck {
    border: 1px #ba181b solid;
    width: fit-content;
    padding: 5px 10px;
    border-radius: 8px;
    color: #ba181b;
    font-weight: 400;
    font-size: small;
    font-family: 'Proxima Soft', sans-serif;
    font-size: small;
    -webkit-transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
    transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
}

.button-blck:hover {
    box-shadow: 0 0 40px 40px #ba181b inset;
    color: #fff;
}